import React from 'react';
import { useParams, Link } from 'react-router-dom';
import mock10 from '../assets/images/mock10.png';
import '../assets/styles/Documentation.scss';
import mock09 from '../assets/images/mock09.png';
import iter_mul from '../assets/images/iter_mul.png';

function Documentation() {
  const params = useParams<{ projectId?: string }>();
  const projectId = params.projectId || null;

  if (!projectId) {
    return (
      <div className="container">
        <div className="doc-container">
          <h1>Documentation</h1>
          <p>No project selected. Go back to the projects section and choose a project.</p>
          <Link to="#" className="back-link">← Back</Link>
        </div>
      </div>
    );
  }
  // Render specific docs per project id
  if (projectId === 'c2s2') {
    return (
      <div className="container">
        <div className="doc-container">
          <Link to="/" className="back-link">← Back</Link>
          <h1>Cornell Custom Silicon Systems</h1>
          <img src={mock10} alt="project hero" className="doc-hero"/>
          <section className="doc-content">
            <h2>Overview</h2>
            <p>
              Cornell Custom Silicon Systems (C2S2) is a student-run organization at Cornell University 
              that focuses on designing and fabricating custom integrated circuits (ICs). The team 
              provides students with hands-on experience in chip design, layout, and testing, allowing them to 
              apply their theoretical knowledge in a practical setting. We collaborate with industry partners 
              and utilize advanced fabrication processes to create high-performance and energy-efficient chips for various applications, 
              including communications, signal processing, and machine learning.
            </p>

            <h3>Quick links</h3>
            <ul>
              <li><a href="https://c2s2.engineering.cornell.edu/" target="_blank" rel="noreferrer">Project Team Website</a></li>
            </ul>

            <h2>My Role</h2>
            <p>
                As a digital/physical design team member, I contributed to the backend design and physical implementation of custom integrated circuits, as
                well as the development of automation scripts to streamline the design process and occasional RTL block design. Some of tasks, past and current, include:
                <ul>
                    <li>Writing scripts in .tcl, bash, Python for a light but robust RTL - GDS flow with verification signoff using FFGL and BAGL testing for
                        Analog/Digital/RF chips with 20 day turnaround, using Synopsys/Cadence tooling, leading to successful 
                        tapeout of chips on TSMC180.</li>
                    <li>Using Slurm and tooling scripts to create a parallelized hierarchical flow for recipe execution and management as efficiently as 
                        possible by utilizing the server hardware to its fullest extent. 
                    </li>
                    <li>Helping with and understanding the RTL design and verification process to properly plan floorplanning and placement
                        strategies when experimenting with SRAMs and other macros.
                    </li>
                </ul>
            </p>
          </section>
        </div>
      </div>
    );
  }

  if (projectId === 'TRV2_processor') {
    return (
      <div className="container">
        <div className="doc-container">
          <Link to="/" className="back-link">← Back</Link>
          <h1>TinyRiscV2 Processor</h1>
          <img src={mock09} alt="TinyRiscV2 hero" className="doc-hero"/>
          <section className="doc-content">
            <h2>Overview</h2>
            <p>
                This is a compact TinyRISCV2 compatible processor implementation, with five forwarded pipeline stages, an 
                iterative multiplication unit, a branch target buffer, and more.
            </p>
            <h3>Quick links</h3>
            <ul>
              <li><a href="https://github.com/smokevan" target="_blank" rel="noreferrer">GitHub</a></li>
            </ul>
            <h2>Design Process</h2>
            <p>
                This processor was in part designed for ECE4750, Computer Architecture, at Cornell University. Using some previously verified IP,
                such as muxes, registers, a regfile, and a memory interface, I designed the processor from the ground up in SystemVerilog. This processor uses 
                a common 5-stage pipeline, with instruction fetch, decode, execute, memory, and writeback stages. It implements full forwarding
                and hazard detection to stall when necessary for multiplication, processor management, and memory operations. 
                <br/>
                 <br/>
                This was done iteratively, starting with simple instructions and gradually adding more complex functionality, testing each step of the way with a
                comprehensive testing/verification. The main testing tool during development was PyMTL3, integrated with Verilator for a source-sink style testing setup that
                was paired with a functional level model in python for random testing reference. Through this project, I was able to learn to devise a testing framework
                that combined unit and integration testing, using both directed and random testing to ensure correctness relative to specification.
                <br/> <br/>
            </p>
            <h3>Iterative Multiplier</h3>
            <img src={iter_mul} alt="iterative multiplier diagram" className="diagram"/>
            <p>
                The iterative multiplier uses a common shift-and-add algorithm to perform multiplication over multiple cycle, with a variable latency system
                to optimize speed by shifting by several indices when many zeroes are present in the multiplier or multiplicand. This is handled through a standard
                val/rdy streaming interface, allowing for careful handling of multiplication operations in the pipeline using hazard detection and stalling. 
            </p>
          </section>
        </div>
      </div>
    );
  }

  // Fallback for unknown ids
  return (
    <div className="container">
      <div className="doc-container">
        <h1>Documentation</h1>
        <p>Project not found.</p>
        <Link to="/" className="back-link">← Back</Link>
      </div>
    </div>
  );
}

export default Documentation;
